.RechargeBox {
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .WordArt {
  font-family: "Yuanti SC";
  font-size: 14px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: white;
  text-shadow: 1px 0px 0px #7a444b, -1px 0px 0px #7a444b, 0px 1px 0px #7a444b, 0px -1px 0px #7a444b;
  font-weight: bolder;
}
.RechargeBox .publicbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fafabe;
  font-weight: bold;
  text-shadow: 1px 0px 0px #bc8845, -1px 0px 0px #bc8845, 0px 1px 0px #bc8845, 0px -1px 0px #bc8845;
}
.RechargeBox .publicbtn span {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 4%;
}
.RechargeBox .publicinput {
  border: 2px solid #7c584a;
}
.RechargeBox .Alert-container {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0) !important;
  /* 兼容ie几不知道，好像ie5 */
  filter: alpha(opacity=0);
}
.RechargeBox .Alert-child-container {
  position: relative;
  width: 30%;
  height: 14%;
  margin: auto;
  top: 40%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
  background: #1e1531;
  display: flex;
  align-items: center;
  padding-top: 0;
  border-radius: 5px ;
  opacity: 0.9;
}
.RechargeBox .Alert-child {
  position: relative;
  top: 0%;
  width: 55%;
