.RechargeBox {
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .publicbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fafabe;
  font-weight: bold;
  text-shadow: 1px 0px 0px #bc8845, -1px 0px 0px #bc8845, 0px 1px 0px #bc8845, 0px -1px 0px #bc8845;
}
.RechargeBox .publicbtn span {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 4%;
}
.RechargeBox .withDrawCashBox_child1 {
  height: 15.7%;
  width: 100%;
  position: relative;
}
.RechargeBox .withDrawCashBox_child1 :nth-child(1) {
  position: absolute;
  left: 1%;
  top: 10%;
  width: 6%;
  height: 75%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child1 .withDrawCashBox_child_title {
  width: 32.8%;
  height: 88%;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 auto;
}
.RechargeBox .withDrawCashBox_child1 .withDrawCashBox_child_title :nth-child(1) {
  position: relative;
  width: 24%;
  height: 70%;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child2 {
  position: relative;
  width: 100%;
  height: 81%;
  padding-top: 1%;
  box-sizing: border-box;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav {
  position: relative;
  float: left;
  width: 17.6%;
  height: 87%;
  padding-top: 0%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav div {
  position: relative;
  height: 18%;
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-top: 5%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav .cur {
  transform: scale(1.16, 1);
  transform-origin: left center;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav .cur img {
  width: 90%;
  margin-right: 10%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_content {
  position: relative;
  float: left;
  width: 80.5%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  filter: alpha(opacity=60);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_content .withDrawCashBox_inner_box {
  position: relative;
  background: #d9f8fb;
  border-radius: 7px;
  width: 97.8%;
  height: 96%;
  opacity: 1;
  box-sizing: border-box;
  padding: 2%;
  box-shadow: 5px 0 2px #bbd6d9 inset, -5px 0 2px #bbd6d9 inset, 0px 5px 2px #bbd6d9 inset, 5px -5px 2px #bbd6d9 inset;
}
