.List_rechargeBgWhite {
  position: absolute;
  bottom: 0%;
  right: 1.5%;
  width: 96%;
  height: 90%;
  overflow: hidden;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 {
  width: 96%;
  height: 9%;
  margin-left: 3%;
  margin-top: 2%;
  display: flex;
  align-items: center;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 span {
  float: left;
  height: 100%;
  margin-top: 0;
  text-align: center;
  display: inline-block;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 :nth-child(1) {
  width: 20%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 :nth-child(2) {
  width: 30%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 :nth-child(3) {
  width: 25%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child1 :nth-child(4) {
  width: 30%;
  transform: scale(0.8, 1);
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 {
  margin-top: 1%;
  border-radius: 5px;
  width: 97%;
  height: 68%;
  background-size: contain;
  margin-left: 1%;
  color: #6f4934;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist {
  margin: 0;
  width: 98%;
  height: 16.7%;
  font-size: 13px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist span {
  margin: 0;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(1) {
  width: 19%;
  margin-left: 2%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(2) {
  margin-left: 2%;
  width: 10%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(3) {
  margin-left: 2%;
  width: 11%;
  background: #b88f19;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  color: white;
  font-weight: bold;
  text-shadow: 1px 0px 0px #bc8845, -1px 0px 0px #bc8845, 0px 1px 0px #bc8845, 0px -1px 0px #bc8845;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(4) {
  text-align: start;
  width: 15%;
  margin-left: 5%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(5) {
  width: 8%;
  text-align: start;
  text-decoration: underline;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(6) {
  margin-left: 5%;
  width: 20%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child2 .orderlist :nth-child(7) {
  width: 8%;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child3 {
  position: relative;
  top: 2%;
  width: 50%;
  height: 15%;
  margin: 0 auto;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child3 :nth-child(1) {
  float: left;
  margin: 0;
  width: 40%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child3 :nth-child(2) {
  margin: 0;
  float: left;
  width: 20%;
  height: 100%;
  text-align: center;
  line-height: 300%;
  color: #8c5c4a;
}
.List_rechargeBgWhite .List_rechargeBgWhite_child3 :nth-child(3) {
  margin: 0;
  float: left;
  width: 40%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left;
}
.details-child_child1 {
  width: 80%;
  height: 90%;
  margin-left: 15%;
  padding-top: 2%;
  text-align: start;
  color: #8c5c4a;
}
.list-child {
  position: relative;
  top: 12%;
  width: 90%;
  height: 55%;
  margin: auto;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  background: #d6a67f;
  box-shadow: 0 5px 4px 0px #516267;
  border-radius: 5px;
  padding-left: 10%;
  padding-right: 5%;
  padding-top: 2%;
  box-sizing: border-box;
}
.list-child .list-child_icon {
  position: absolute;
  left: 3%;
  top: 7%;
  width: 7%;
  height: 20%;
  background-size: contain;
  background-repeat: no-repeat;
}
.list-child p {
  text-align: start;
  color: #8c5c4a;
  font-weight: bold;
}
