.dcBody {
  width: 100%;
  height: 100%;
  position: relative;
  left: 1%;
}
.dcBody .dcP1 {
  width: 80%;
  height: 8%;
  position: relative;
  left: 2%;
  top: 8%;
  font-size: 12px;
  color: #6f4934;
}
.dcBody .userInput {
  position: relative;
  left: 2%;
  top: 12%;
  height: 12%;
  width: 55%;
}
.dcBody .userInput div {
  height: 100%;
  width: 100%;
  background: #9f7463;
}
.dcBody .userInput div input {
  width: 90%;
  float: left;
  height: 85%;
  font-size: 15px;
  opacity: 1;
  border-radius: 5px;
  background: transparent;
  position: relative;
  color: white;
  left: 4%;
  top: 4%;
  border: transparent;
  outline: none;
}
.dcBody .userInput .deleBtn {
  width: 5.5%;
  height: 40%;
  position: absolute;
  right: 2%;
  top: 30%;
}
.dcBody .userInput .deleBtn img {
  width: 100%;
  height: 100%;
}
.dcBody .dcP2 {
  height: 10%;
  position: absolute;
  left: 0%;
  top: 35%;
  color: red;
}
.dcBody .czBtn {
  position: absolute;
  left: 60%;
  top: 21%;
  width: 20%;
  height: 13%;
  float: left;
  border-radius: 5%;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: bold;
}
.dcBody .dcP3 {
  color: #6f4934;
  position: absolute;
  top: 46%;
  left: 0;
  width: 98%;
  height: 29%;
  margin: 0 auto;
  background: #ceaa9c;
  box-sizing: border-box;
  padding-top: 2%;
  padding-left: 2%;
  background-size: contain;
  background-repeat: no-repeat;
}
.czBtn2 {
  width: 20%;
  height: 13%;
  position: absolute;
  right: 5%;
  bottom: 4%;
  background-size: contain;
  background-repeat: no-repeat;
}
.CZZY-child {
  width: 92%;
  position: relative;
  -webkit-overflow-scrolling: touch;
  left: 5%;
  top: 10%;
  height: 82%;
  overflow: auto;
}
.CZZY-child div {
  width: 100%;
  height: 700%;
  background-size: contain;
  background-repeat: no-repeat;
}
