.ZfbBox1 {
  width: 98%;
  height: 46%;
  margin-left: 2%;
}
.ZfbBox1 .ZfbBox1_child1 {
  position: relative;
  top: 25%;
  height: 26%;
  display: flex;
  align-items: center;
}
.ZfbBox1 .ZfbBox1_child1 p {
  width: 18%;
  float: left;
  text-align: center;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox {
  width: 45%;
  height: 100%;
  position: relative;
  left: 2%;
  background: #9f7463;
  float: left;
  background-repeat: no-repeat;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div i {
  font-style: normal;
  width: 7%;
  height: 50%;
  position: absolute;
  right: 2%;
  top: 25%;
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form {
  position: relative;
  width: 100%;
  left: 0%;
  margin-top: 3%;
  color: white;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form #divselect {
  position: relative;
  z-index: 100;
  border: transparent;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form #divselect cite {
  width: 90%;
  display: inline-block;
  margin-left: 10%;
  font-style: normal;
  margin-bottom: 3%;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form #divselect ul {
  list-style: none;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form #divselect ul li {
  border-top: 1px solid white;
  width: 100%;
  height: 20%;
  background: #9f7463;
  opacity: 0.8;
}
.ZfbBox1 .ZfbBox1_child1 .selectBox div form #divselect ul li a {
  text-decoration: none;
  color: white;
  display: inline-block;
  width: 90%;
  height: 100%;
  padding-left: 10%;
  margin-top: 2%;
  margin-bottom: 2%;
}
.ZfbBox1 .ZfbBox1_child2 {
  position: relative;
  top: 36%;
  height: 26%;
  display: flex;
  align-items: center;
}
.ZfbBox1 .ZfbBox1_child2 p {
  width: 18%;
  float: left;
  text-align: center;
}
.ZfbBox1 .ZfbBox1_child2 div {
  width: 45%;
  height: 100%;
  position: relative;
  left: 2%;
  background: #9f7463;
  float: left;
  background-repeat: no-repeat;
}
.ZfbBox1 .ZfbBox1_child2 div input {
  width: 80%;
  height: 100%;
  margin-left: 10%;
  background: transparent;
  border: transparent;
  outline: none;
  color: white;
}
.ZfbBox1 .ZfbBox1_child2 div i {
  width: 7%;
  height: 50%;
  position: absolute;
  right: 2%;
  top: 25%;
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
}
.ZfbBox1 .ZfbBox1_child2 span {
  display: inline-block;
  position: absolute;
  right: -2%;
  width: 40%;
  font-size: 12px;
  transform: scale(0.8, 1);
  color: #c72d26;
}
.ZfbBox2 {
  width: 98%;
  height: 29%;
  margin: 0 auto;
  background: #ceaa9c;
  box-sizing: border-box;
  padding-top: 2%;
  padding-left: 1%;
  background-size: contain;
  background-repeat: no-repeat;
}
.ZfbBox2 .ZfbBox2_child1 {
  width: 18%;
  height: 35%;
  text-align: center;
}
.ZfbBox2 .ZfbBox2_child2 {
  width: 100%;
  height: 50%;
  margin-top: 2%;
}
.ZfbBox2 .ZfbBox2_child2 p {
  width: 14%;
  margin-left: 2%;
  height: 65%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  color: white;
  font-size: 14px;
}
.ZfbBox2 .ZfbBox2_child2 p span {
  transform: scale(0.9, 1);
}
.ZfbBox3 {
  width: 20%;
  height: 12%;
  position: absolute;
  bottom: 5%;
  right: 5%;
  background-size: contain;
  background-repeat: no-repeat;
}
.ZfbBox3 span {
  margin-top: 2%;
}
